/* src/App.css */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.job-card {
  background-color: #f9f9f9;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.job-info h4 {
  margin: 0;
}

.button-container {
  display: flex;
  gap: 10px;
  align-items: center;
}

.trash-btn {
  background-color: red;
  color: white;
  border: none;
  padding: 5px 10px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.trash-btn:hover {
  background-color: darkred;
}

.add-job-form {
  margin-top: 20px;
}

.add-job-form form {
  display: flex;
  flex-direction: column;
}

.add-job-form label {
  margin: 5px 0;
}

.add-job-form input, .add-job-form select {
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.add-job-form button {
  padding: 8px 15px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.add-job-form button[type="button"] {
  background-color: #f44336;
}

.add-job-form button:hover {
  background-color: #45a049;
}

.add-job-form button[type="button"]:hover {
  background-color: #e53935;
}
